<template>
    <div class="home-container">
        <div class="home-main-body">
            <div class="home-head-name">
                Assassin<span>Go</span>
            </div>
            <div class="warning" v-show="warningStatus">{{warningText}}</div>
            <div class="home-input-group">
                <input type="text" class="home-input" autofocus v-model="targetURL" @keyup.enter="setTarget">
                <div class="submit-button" @click="setTarget">Set Target</div>
            </div>
            <div class="home-head-name" style="visibility: hidden">
                Assassin<span>Go</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            //title name
            NAME: 'AssassinGo',
            //target
            targetURL: "",
            ///
            warningStatus: false,
            //
            warningText: ""
        }
    },
    //methods vue
    methods: {
        /*
         * 
         * @clickSet Target DIV
        */
        setTarget () {
            //datathis.
            if(this.targetURL != "") {
                //
                this.warningStatus = false;

                //Ajax
                // const ES6  
                const url = '/api/target';
                const data = {
                    target: this.targetURL
                };

                //this.ajax_post()ajax POST tools/ajax.js
                //then responsejson
                this.ajax_post(url, data).then(response => {
                    //
                    if(response.flag == 1) {
                        //target
                        sessionStorage.setItem("target", data.target);
                        this.loadStatus.clear();
                        //$router/recon
                        this.$router.push({
                            path: '/recon',
                        });
                    }
                }).catch(error => console.log(error));
            }
            else {
                //
                this.warningText = "";
                //
                this.warningStatus = true;
            }
        },
    },
}
</script>

<style scoped>
.warning {
    font-size: 10px;
    color: brown;
}
.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.home-head-name {
    font-size: 44px;
    color: black;
}
.home-head-name > span {
    color: #e56845;
}
.home-input {
    height: 50px;
    width: 500px;
    font-size: 18px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    box-sizing: border-box;
    padding-left: 10px;
    border: 1px solid gray;
    border-right: none;
}
.home-input:focus {
    outline: none;
    border: 1px solid gray;
    border-right: none;
}
.home-input-group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
}
.submit-button {
    height: 50px;
    width: 90px;
    box-sizing: border-box;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    line-height: 50px;
    text-align: center;
    border: 1px solid gray;
    cursor: pointer;
    /* transition:  all .5s linear; */
    background: white;
}
.submit-button:hover {
    background: #e56845;
    color: white;
    border: none;
}
</style>


